@import '../../common/sass/variables';
@import '../../common/sass/media';

.wrapper {
  display: flex;
  flex-basis: 20%;
  position: relative;
  background: $backgr;
  flex-direction: column;
  align-items: center;
  padding: 30px 10px;

  .btnsWrapper {
    display: flex;
    width: 100%;
    justify-content: space-around;
    margin-bottom: 40px;

    & button {
      width: 40%;
    }
  }
}

.title{
  padding-bottom: 15px;
}

@include d-media(sm) {
  .wrapper {
    position: absolute;
    width: 100%;
    padding: 10px;
    top: 60px;
    background: none;

    .title {
      display: none;
    }

    .btnsWrapper {
      margin-bottom: 10px;

      & button {
        padding: 0.5rem 1rem;
      }
    }
  }
}
